@import url(https://fonts.googleapis.com/css?family=Roboto:400,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    font-family: 'Roboto', sans-serif;
}
.color-white{
    color: rgb(255, 255, 255);
}
.App-title {
 font-size: 100px;
 font-weight: 30px;
}

.App-subtitle {
    font-size: 60px;
    font-family: 'Roboto', sans-serif;
}

.color-black {
    background-color: black;
}

.body{
    background-color: black;
}

.center {
    text-align: center;
    margin-left: 3%;
}

.input {
    background: transparent;
    border: 1px solid rgba(7, 93, 128, 0.603);
    width: 400px;
    height: 30px;
    margin-left: 35%;
    color: white;
}

.cards {
    background: #56CCF2;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #2F80ED, #56CCF2);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, right top, left top, from(#2F80ED), to(#56CCF2));
background: -webkit-linear-gradient(right, #2F80ED, #56CCF2);
background: linear-gradient(to left, #2F80ED, #56CCF2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 width: 100px;
 height: 150px;
/* margin-right: 5px; */
}

.hero-title{
    font-size: 30px;
}
.body-character {
    border: 1px solid rgb(168, 36, 36);
    
}
.btn-volver {
    background: transparent;
    color: aliceblue;
    border: none;
}
